import { FC } from "react";
import classNames from "classnames/bind";

import { Theme } from "common/constants/enum";
import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";

import { TextWithIconProps } from "./TextWithIcon.types";
import styles from "./TextWithIcon.module.scss";

const cx = classNames.bind(styles);

export const TextWithIcon: FC<TextWithIconProps> = ({
    icon,
    text,
    reverse,
    theme = Theme.DARK,
    className,
}) => (
    <div
        className={cx(className, "root", `theme_${theme}`, {
            root_reverse: reverse,
        })}
    >
        <Icon
            className={cx("icon", {
                icon_reverse: reverse,
            })}
        >
            {icon}
        </Icon>
        <Text
            className={cx("text", {
                text_reverse: reverse,
            })}
        >
            {text}
        </Text>
    </div>
);
