import { FC } from "react";
import classNames from "classnames/bind";

import { Theme, WINDOW_SIZE } from "common/constants/enum";
import { PriceLabel } from "common/components/new/PriceLabel";
import { Picture } from "common/components/Picture";
import { Text } from "common/components/Text";
import { TextWithIcon } from "common/components/TextWithIcon";
import { Video } from "common/components/Video";
import { Ticker, TickerPositionType } from "common/components/Ticker";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import { renderIcon } from "../helpers";
import { AdvantagesGridItemProps } from "./AdvantagesGridItem.types";
import styles from "./AdvantagesGridItem.module.scss";

const cx = classNames.bind(styles);

export const AdvantagesGridItem: FC<AdvantagesGridItemProps> = (
    {
        title,
        description,
        image,
        linkVideo,
        linkGif,
        points,
        price,
        language,
        version,
        onButtonClick,
        theme = Theme.DARK,
    },
    index: number
) => {
    const headingType = theme === Theme.LIGHT ? HeadingType.XL : HeadingType.M;

    const itemPicture = image && (
        <Picture
            className={cx("item__picture")}
            classNameImg={cx("item__image")}
            mobile={image.mobile}
            desktop={{
                ...image.desktop,
                media: "(min-width: 1024px)",
            }}
        />
    );

    const itemPictureWithTicker = image && (
        <div
            className={cx("item__image-wrapper", {
                "item__image-wrapper_is-full-width": image.isFullWidth,
            })}
        >
            {image.tickerText && (
                <Ticker
                    value={image.tickerText}
                    className={cx("item__ticker")}
                />
            )}
            {itemPicture}
            {image.tickerText && (
                <Ticker
                    value={image.tickerText}
                    position={TickerPositionType.BOTTOM}
                    className={cx("item__ticker")}
                />
            )}
        </div>
    );

    return (
        <div
            className={cx("item", { [`item_theme-${theme}`]: theme })}
            key={index}
        >
            <div className={cx("item__content")}>
                <Heading
                    tag={HeadingTag.H4}
                    type={headingType}
                    className={cx("item__title", {
                        "item__title_no-description": !description,
                    })}
                >
                    {title}
                </Heading>
                {description && (
                    <Text className={cx("item__description")}>
                        {description}
                    </Text>
                )}
                {points && points.length > 0 && (
                    <div className={cx("item__points")}>
                        {points.map((item, index) => (
                            <TextWithIcon
                                {...item}
                                icon={renderIcon(item.icon as string, theme)}
                                theme={theme}
                                className={cx("item__point")}
                                key={index}
                            />
                        ))}
                    </div>
                )}
                {price && onButtonClick && (
                    <PriceLabel
                        {...price}
                        onButtonClick={onButtonClick}
                        language={language}
                        version={version}
                        isThemeLight={theme === Theme.LIGHT}
                    />
                )}
            </div>
            {image?.tickerText ? itemPictureWithTicker : itemPicture}

            {linkVideo && (
                <div
                    className={cx(
                        "item__picture"
                        // "item__picture_desktop-only"
                    )}
                >
                    <Video
                        className={cx("item__video")}
                        autoPlay={true}
                        loop={true}
                        muted={true}
                        playsInline={true}
                        src={linkVideo}
                    />
                </div>
            )}

            {linkGif && (
                <div
                    className={cx(
                        "item__image-wrapper"
                        // "item__image-wrapper_mobile-only"
                    )}
                >
                    <Picture
                        mobile={{
                            gif: linkGif,
                            media: `(max-width: ${WINDOW_SIZE.DESKTOP}px)`,
                        }}
                        className={cx("item__picture")}
                        classNameImg={cx("item__gif")}
                        alt="promo"
                    />
                </div>
            )}
        </div>
    );
};
