import { FC } from "react";
import classNames from "classnames/bind";

import { Theme } from "common/constants/enum";
import { Heading, HeadingTag } from "common/components/new/Heading";

import { AdvantagesGridItem } from "./AdvantagesGridItem";
import { AdvantagesGridProps } from "./AdvantagesGrid.types";
import styles from "./AdvantagesGrid.module.scss";

const cx = classNames.bind(styles);

export const AdvantagesGrid: FC<AdvantagesGridProps> = ({
    title,
    list,
    classNameTitle,
    language,
    version,
    onButtonClick,
    theme = Theme.DARK,
}) => {
    return (
        <section>
            {title && (
                <Heading
                    className={cx(classNameTitle, "title")}
                    tag={HeadingTag.H2}
                >
                    {title}
                </Heading>
            )}
            {list.map((item, index) =>
                AdvantagesGridItem(
                    {
                        ...item,
                        onButtonClick,
                        theme,
                        language,
                        version,
                    },
                    index
                )
            )}
        </section>
    );
};
