import { FC } from "react";
import classNames from "classnames/bind";

import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Text } from "common/components/Text";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";
import { OldPrice, OldPriceTheme } from "common/components/OldPrice";
import { priceFormatter } from "common/utils/priceFormatter";

import { PriceLabelProps } from "./PriceLabel.types";
import styles from "./PriceLabel.module.scss";

const cx = classNames.bind(styles);

export const PriceLabel: FC<PriceLabelProps> = ({
    monthly,
    once,
    payTypeMonthly,
    payTypeOnce,
    month,
    period,
    interest,
    currency,
    button,
    legalSorteo,
    legalPrice,
    prepayment,
    version,
    onButtonClick,
    isThemeLight,
}) => (
    <div className={cx("root", { "root_theme-light": isThemeLight })}>
        <div className={cx("monthly", "row")}>
            <Text className={cx("payment-type")}>{payTypeMonthly}</Text>
            <div className={cx("price")}>
                {monthly.oldPrice && (
                    <OldPrice
                        content={priceFormatter(
                            monthly.oldPrice,
                            currency,
                            version
                        )}
                        theme={OldPriceTheme.GRAY}
                        className={cx("price__old")}
                    />
                )}
                <div className={cx("price__new")}>
                    <span
                        className={cx("amount", {
                            amount_red: monthly.oldPrice,
                        })}
                    >
                        {monthly.amount}
                    </span>
                    <div className={cx("column")}>
                        <span>{month}</span>
                        <span>{period}</span>
                    </div>
                </div>
                {prepayment && (
                    <Text className={cx("prepayment")}>{prepayment}</Text>
                )}
                {interest && <Text className={cx("interest")}>{interest}</Text>}
                {legalSorteo && (
                    <Text className={cx("legal")}>{legalSorteo}</Text>
                )}
            </div>
        </div>
        <div className={cx("once", "row")}>
            <Text className={cx("payment-type")}>{payTypeOnce}</Text>
            <div className={cx("price")}>
                {once.oldPrice && (
                    <OldPrice
                        content={priceFormatter(
                            once.oldPrice,
                            currency,
                            version
                        )}
                        theme={OldPriceTheme.GRAY}
                        className={cx("price__old")}
                    />
                )}
                <div className={cx("amount", "amount_red")}>
                    {priceFormatter(once.amount, currency, version)}
                </div>
            </div>
        </div>

        <Button
            id={getAnalyticsId({
                zone: "price",
                label: "label",
            })}
            onClick={onButtonClick}
            size={ButtonSize.M}
            theme={isThemeLight ? ButtonTheme.TRANSPARENT : ButtonTheme.RED}
            className={cx("action")}
        >
            {button.name}
        </Button>

        <Text className={cx("legal", "legal_price")}>{legalPrice}</Text>
    </div>
);
