import { Theme } from "common/constants/enum";
import EasyIcon from "common/components/Icons/icons/48/easy.svg";
import EasyWhiteIcon from "common/components/Icons/icons/48/easy-white.svg";
import FollowUpIcon from "common/components/Icons/icons/48/follow-up.svg";
import FollowUpWhiteIcon from "common/components/Icons/icons/48/follow-up-white.svg";
import ChangeIcon from "common/components/Icons/icons/48/change.svg";
import ChangeWhiteIcon from "common/components/Icons/icons/48/change-white.svg";
import Scan3DDarkIcon from "common/components/Icons/icons/48/scan-3d-dark.svg";
import Scan3DWhiteIcon from "common/components/Icons/icons/48/scan-3d-white.svg";
import XRayIcon from "common/components/Icons/icons/48/xray.svg";
import XRayWhiteIcon from "common/components/Icons/icons/48/xray-white.svg";
import The3DIcon from "common/components/Icons/icons/48/3d.svg";
import The3DWhiteIcon from "common/components/Icons/icons/48/3d-white.svg";

export const renderIcon = (iconName: string, theme: Theme) => {
    if (theme === Theme.LIGHT) {
        switch (iconName) {
            case "easy48":
                return <EasyWhiteIcon />;
            case "followUp48":
                return <FollowUpWhiteIcon />;
            case "change48":
                return <ChangeWhiteIcon />;
            case "scan3DDark48":
                return <Scan3DWhiteIcon />;
            case "xray48":
                return <XRayWhiteIcon />;
            case "the3D48":
                return <The3DWhiteIcon />;
            default:
                return null;
        }
    }

    switch (iconName) {
        case "easy48":
            return <EasyIcon />;
        case "followUp48":
            return <FollowUpIcon />;
        case "change48":
            return <ChangeIcon />;
        case "scan3DDark48":
            return <Scan3DDarkIcon />;
        case "xray48":
            return <XRayIcon />;
        case "the3D48":
            return <The3DIcon />;
        default:
            return null;
    }
};
