import { FC } from "react";
import classNames from "classnames/bind";

import { TickerPositionType, TickerProps } from "./Ticker.types";
import styles from "./Ticker.module.scss";

const cx = classNames.bind(styles);
const COUNT_TICKER_WORDS_MOBILE = 10;

export const Ticker: FC<TickerProps> = ({
    value,
    position = TickerPositionType.TOP,
    className,
}) => (
    <div className={cx(className, "root", `root_position_${position}`)}>
        <ul className={cx("ticker")}>
            {new Array(COUNT_TICKER_WORDS_MOBILE)
                .fill(value)
                .map((text, index) => {
                    return (
                        <li key={index} className={cx("ticker-item")}>
                            {text}
                        </li>
                    );
                })}
        </ul>
    </div>
);
